.image_accueil {
  width: 97.5%;
  height: 97.5%;
  margin-left: 20px;
}

#contenu_slider {
  position: fixed;
  top: 390px;
  right: 365px;
}

.bouton_accueil {
  width: 270px;
  height: 55px;
  background-color: white;
  display: inline-block;
  outline: none;
  cursor: pointer;
  font-size: 14px;
  line-height: 1;
  border-radius: 500px;
  transition-duration: 0.3s;
  transition-property: background-color, border-color, color, box-shadow, filter;
  border: 1px solid transparent;
  letter-spacing: 2px;
  min-width: 160px;
  text-transform: uppercase;
  white-space: normal;
  font-weight: 700;
  text-align: center;
  padding: 16px 14px 18px;
}

.bouton_accueil:hover {
  color: #fff;
  background-color: #616467;
}
